'use client'
import React from 'react';
import { Mail, Plus, MoreHorizontal } from 'lucide-react';

const GmailInbox = () => {
  const emails = [
    { 
      id: 1, 
      subject: "Complaints", 
      preview: "Re: TAP TAP SEND issues\nDear Customer, Assalam-o-Alaikum In this regard, it is to infor...",
      time: "11:31 AM",
      tag: "Banking Notifications",
      important: true
    },
    { 
      id: 2, 
      subject: "Temu", 
      preview: "Please confirm it ASAP\nHere's an amazing offer to say Thank You!",
      time: "10:56 AM"
    },
    { 
      id: 3, 
      subject: "Slack", 
      preview: "Slack confirmation code: J6A-ZAK\nConfirm your email address. Here's your confirmation code. Yo...",
      time: "Aug 4",
      tag: "Security Alerts"
    },
    { 
      id: 4, 
      subject: "martin@trymartin.com", 
      preview: "Hi from Martin!\nHey All! Martin here. Your email is now synced with me. Feel fr...",
      time: "Aug 4",
      isEmail: true
    }
  ];

  return (
    <div className="bg-gray-900/20 backdrop-blur-md border border-gray-700/30 rounded-lg p-4">
      <div className="flex items-center justify-between mb-4">
        <h2 className="text-white font-medium">Inbox</h2>
        <div className="flex items-center gap-2">
          <MoreHorizontal className="w-4 h-4 text-gray-400" />
        </div>
      </div>

      <div className="flex items-center gap-2 mb-4">
        <div className="text-gray-400 text-xs">raliather303@gmail.com</div>
      </div>

      <div className="flex gap-2 mb-4">
        <button className="text-white text-sm bg-gray-800/50 px-3 py-1 rounded">Important</button>
        <button className="text-gray-400 text-sm px-3 py-1">Other</button>
      </div>

      <div className="space-y-3">
        {emails.map((email) => (
          <div key={email.id} className="p-3 hover:bg-gray-800/30 rounded-md cursor-pointer border-l-2 border-transparent hover:border-blue-500">
            <div className="flex items-center gap-2 mb-1">
              {email.important && <div className="w-2 h-2 bg-red-500 rounded-full"></div>}
              <span className="text-white font-medium text-sm">{email.subject}</span>
              {email.tag && (
                <span className="text-xs bg-green-500/20 text-green-400 px-2 py-0.5 rounded ml-auto">
                  {email.tag}
                </span>
              )}
              <span className="text-gray-400 text-xs">{email.time}</span>
            </div>
            <p className="text-gray-300 text-xs leading-relaxed">{email.preview}</p>
            {email.isEmail && (
              <div className="flex items-center gap-1 mt-2">
                <Mail className="w-3 h-3 text-gray-400" />
                <Plus className="w-3 h-3 text-gray-400" />
              </div>
            )}
          </div>
        ))}
      </div>

      <div className="mt-4 pt-4 border-t border-gray-700/30">
        <h3 className="text-white font-medium text-sm mb-2">Email Briefing</h3>
      </div>
    </div>
  );
};

export default GmailInbox;